<script>
export default {
  data () {
    return {
      fixed: false,
      expanded: false,
      expanded2: false,
      expanded3: false,
      card_num: '',
      card_date: '',
      card_security: '',
      choice1: false,
      choice2: false,
      choice3: false,
      buyItem: [],
      model: null,
      options: [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', ''
      ],
      name: '',
      telephonenumber: '',
      mail: '',
      post: '',
      city: '',
      address: '',
      date: '',
      pay: '',
      user: '',
      theshipping: '',
      totalprice: '',
      visible: false,
      delieverydate: '',
      delieverynumber: '',
      info: '',
      ordernow: ''
    }
  },
  mounted () {
    const buyItem = this.$store.state.cartItem1
    this.buyItem = buyItem
    const day = new Date()
    const getMonth = day.getMonth() + 1
    const date = day.getFullYear() + '/' + getMonth + '/' + day.getDate()
    this.date = date
    this.user = this.$store.state.user.id
    const theshipping = this.$store.getters.shipping
    this.theshipping = theshipping
    const totalprice = this.$store.getters.totalPrice2 + this.$store.getters.shipping
    this.totalprice = totalprice
  },
  methods: {
    btnok1 (value) {
      this.pay = value
      if (this.card_num.length !== 0 && this.card_date.legth !== 0 && this.card_security.length !== 0) {
        console.log(value)
        this.expanded = false
        this.choice1 = true
        this.choice2 = false
        this.choice3 = false
      } else {
        this.$swal({
          icon: 'warning',
          title: ''
        })
      }
    },
    btnok2 (value) {
      this.pay = value
      console.log(value)
      this.expanded2 = false
      this.choice2 = true
      this.choice1 = false
      this.choice3 = false
    },
    btnok3 (value) {
      this.pay = value
      console.log(value)
      this.expanded3 = false
      this.choice3 = true
      this.choice1 = false
      this.choice2 = false
    },
    shipping () {
      return this.$store.getters.shipping
    },
    // 
    totalPrice2 () {
      return this.$store.getters.totalPrice2
    },
    onSubmit () {
      if (this.pay.length === 0) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
      } else {
        this.fixed = true
        this.$swal({
          icon: 'warning',
          title: '',
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#000',
          cancelButtonColor: '#d33',
          confirmButtonText: '',
          cancelButtonText: ''
        }).then((value) => {
          if (value.isConfirmed) {
            this.visible = true
            const obj = { ...this.$data }
            delete obj.expanded
            delete obj.expanded2
            delete obj.expanded3
            delete obj.model
            delete obj.options
            console.log(obj)
            this.axios.post(process.env.VUE_APP_API + '/order', obj)
              .then(res => {
                if (res.data.success) {
                  this.visible = false
                  this.$swal({
                    icon: 'success',
                    title: '',
                    text: ':' + res.data.result._id
                  })
                }
                this.$store.commit('resetcartItem1')
                this.$router.push('/onlineshop')
              }).catch((error) => {
                console.log(error)
                this.visible = false
                alert('')
              })
          } else {
            this.visible = false
            this.$swal({
              icon: 'success',
              title: ''
            })
          }
        })
      }
    }
  }
}
</script>
